import './App.css';
import YesScreen from "./components/YesScreen";
import { useState } from "react";
import FirstScreen from './components/FirstScreen';

function App() {

  const [showYesScreen, setShowYesScreen] = useState(false);

  const handleYesScreen = () => {
    setTimeout(() => {
      setShowYesScreen(true);
    }, 1000)
  }

  return (
    <div className="screen">
        {!showYesScreen && <FirstScreen handleYesScreen={handleYesScreen} />}
        {showYesScreen && <YesScreen />}
    </div>
  );
}

export default App;
