import Container from "./Container";
import Header from "./Header";
import Question from "./Question";
import { useState } from "react";

import './FirstScreen.css';
import Modal from "./Modal";

const FirstScreen = (props) => {

    const [showModal, setShowModal] = useState(false);

    const hideFirstScreen = () => {
        document.querySelector('.firstScreen').classList = "firstScreen hiding";
        props.handleYesScreen();
    }

    const handleShowModal = () => {
        setShowModal(true);
    }

    const clickHandlerOk = () => {
        setShowModal(false);
    }

    return (
        <div className="firstScreen">
            <Container>
            <Header />
            <Question hideFirstScreen={hideFirstScreen} handleShowModal={handleShowModal}  />
            {showModal && <Modal clickHandler={clickHandlerOk} />}
            </Container>
        </div>
    );
}

export default FirstScreen;