import Button from "./Button";
import "./Question.css";

import { useEffect } from "react";

const Question = (props) => {
    const clickHandlerYes = () => {
        props.hideFirstScreen();
      }
    
      const clickHandlerNo = () => {
        props.handleShowModal();
      }
    
    useEffect(() => {
        setTimeout(() => {
            console.log('Teste');
            document.querySelector('.letter-image').classList = "letter-image hover";
        }, 3000);
    }, []);
        return (
            <div className="letter-image">
              <div className="animated-mail">
                <div className="back-fold"></div>
                <div className="letter">
                  <div className="letter-border"></div>
                  <div className="question">
                    <div>Quer ir tomar uma cerveja comigo?</div>
                    <div className="question-button">
                        <Button label="Sim" clickHandler={clickHandlerYes} />
                        <Button label="No" clickHandler={clickHandlerNo} />
                    </div>
                  </div>
                </div>
                <div className="top-fold"></div>
                <div className="body"></div>
                <div className="left-fold"></div>
              </div>
            </div>
          );
};

export default Question;
